*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: @font-main;
  font-size: 13px;
  font-weight: 400;
  line-height: 24px;
  font-style: normal;

  margin: 0;
  padding-left: 0;
  padding-right: 0;
  width: 100%;

  color: @black;
  background-color: @white;
}

a {
  text-decoration: none;
  color: @black;
}

img {
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0 0 0 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}
